import React, { useEffect, useState, useCallback } from 'react'
import { useWallet } from '@binance-chain/bsc-use-wallet'
import masterChefABI from 'config/abi/masterchef.json'
import abi from 'config/abi/erc20.json'
import { Heading, Card, CardBody, Button } from 'uikit'
import styled from 'styled-components'
import UnlockButton from 'components/UnlockButton'
import useRefresh from 'hooks/useRefresh'
import { getMasterChefAddress } from 'utils/addressHelpers'
import multicall from 'utils/multicall'
import { useApprove } from 'hooks/useApprove'
import BigNumber from 'bignumber.js'
import contracts from 'config/constants/contracts'
import { useERC20, useMasterchef } from 'hooks/useContract'
import { join, bribe } from 'utils/callHelpers'

const StyledJoinCard = styled(Card)`
  min-height: 376px;
`

const Actions = styled.div`
  margin-top: 24px;
`

const feeMethods = ['bribeFee', 'joinFee', 'joinGift']
const pot = new BigNumber(10).pow(18)
const CHAIN_ID = process.env.REACT_APP_CHAIN_ID

const useGetFees = () => {
  const master = getMasterChefAddress()
  const { account }: { account: string } = useWallet()
  const [bribeFee, setBribeFee] = useState(null)
  const [joinFee, setJoinFee] = useState(null)
  const [joinGift, setJoinGift] = useState(null)
  const [allowanceC, setAllowanceC] = useState(null)
  const [allowanceB, setAllowanceB] = useState(null)
  const { fastRefresh } = useRefresh()

  useEffect(() => {
    const fetchAllFees = async () => {
      const calls = feeMethods.map((x) => ({
        address: master,
        name: x,
        params: [],
      }))

      const [a, b, c] = await multicall(masterChefABI, calls)

      setBribeFee(new BigNumber(a).div(pot))
      setJoinFee(new BigNumber(b).div(pot))
      setJoinGift(new BigNumber(c).div(pot))

      if (!account) return
      const fetchAllowance = [
        { address: contracts['C100'][CHAIN_ID], name: 'allowance', params: [account, master] },
        { address: contracts.busd[CHAIN_ID], name: 'allowance', params: [account, master] },
      ]

      const [d, e] = await multicall(abi, fetchAllowance)

      setAllowanceC(new BigNumber(d))
      setAllowanceB(new BigNumber(e))
    }

    fetchAllFees()
  }, [fastRefresh, account, master])

  return [bribeFee, joinFee, joinGift, allowanceC, allowanceB]
}

const JoinOrBribeButton: React.FC<any> = ({ allowanceC, joinFee, isJoin }) => {
  const { account }: { account: string } = useWallet()
  const [requestedApproval, setRequestedApproval] = useState(false)
  const isApproved = account && allowanceC && allowanceC.isGreaterThan(0)
  const { onApprove } = useApprove(
    useERC20(contracts[isJoin ? 'C100' : 'busd'][CHAIN_ID]),
    joinFee.times(new BigNumber(10).pow(18)),
  )
  const contract = useMasterchef()
  const label = isJoin ? 'Join' : 'Bribe'

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const handleJoin = useCallback(async () => {
    try {
      setRequestedApproval(true)
      if (isJoin) await join(contract, account)
      else await bribe(contract, account)
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [contract, account, isJoin])

  return (
    <Actions>
      {isApproved ? (
        <Button fullWidth disabled={requestedApproval} onClick={handleJoin}>
          {label}
        </Button>
      ) : (
        <Button fullWidth disabled={requestedApproval} onClick={handleApprove}>
          {`Approve Contract for ${label}`}
        </Button>
      )}
    </Actions>
  )
}

const JoinCard: React.FC = () => {
  const { account } = useWallet()
  const [bribeFee, joinFee, joinGift, allowanceC, allowanceB] = useGetFees()

  return (
    <StyledJoinCard>
      <CardBody>
        <Heading size="lg" mb="24px">
          Join Top Secret Finance
        </Heading>
        <Heading size="md" mb="16px" color="secondary">
          {joinFee && joinGift
            ? `Exchange ${joinFee.toNumber()} C101 Information Token for access and get ${joinGift.toNumber()} A009 Access Level Tokens.`
            : 'Exchange some C101 Information Token for access and get some A009 Access Level Tokens.'}
        </Heading>
        <Heading size="md" mb="16px" color="secondary">
          {bribeFee
            ? `You can also bribe with ${bribeFee.toNumber()} BUSD to access.`
            : 'You can also bribe with some BUSD to access.'}
        </Heading>
        <Heading size="md" mb="16px" color="secondary">
          MasterSpy tip: You can ask another Agent to invite join for free.
        </Heading>
        {account ? (
          <>
            {joinFee && <JoinOrBribeButton allowanceC={allowanceC} joinFee={joinFee} isJoin />}
            {bribeFee && <JoinOrBribeButton allowanceC={allowanceB} joinFee={bribeFee} isJoin={false} />}
          </>
        ) : (
          <Actions>
            <UnlockButton fullWidth />
          </Actions>
        )}
      </CardBody>
    </StyledJoinCard>
  )
}

export default JoinCard
